<template>
  <div id="register">
    <div class="register-form">
        <div class="register-inner">
            <h2>
                <p>考生注册</p>
                <p class="f14">REGISTER</p>
            </h2>
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
                <el-form-item label="用户账号" prop="userId">
                <el-input v-model="ruleForm.userId"
                    placeholder="考号或者报名号"></el-input>
                </el-form-item>
                <el-form-item label="用户昵称" prop="userName">
                <el-input v-model="ruleForm.userName"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="password">
                <el-input type="password" v-model="ruleForm.password"></el-input>
                </el-form-item>
                <el-form-item label="个人邮箱" prop="userEmail">
                <el-input v-model="ruleForm.userEmail"></el-input>
                </el-form-item>
                <el-form-item label="大陆手机号" prop="telephone">
                    <div class="telePhone">
                        <el-input v-model="ruleForm.telephone"></el-input>
                        <el-button @click="sendCode" :disabled="isCode">
                        <span v-show="isCode">
                            <i class="el-icon-loading"></i>
                            {{ retime+'s' }}
                        </span>
                        <span v-show="!isCode">获取手机验证码</span>
                        </el-button>
                    </div>
                </el-form-item>
                <el-form-item label="手机验证码" prop="phoneCode">
                <el-input v-model="ruleForm.phoneCode" autocomplete="off"></el-input>
                </el-form-item>
                <div class="toResister">
                <router-link to="/Login">注册完成,去登录</router-link>
                </div>
                <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')">注册</el-button>
                <el-button @click="resetForm('ruleForm')">重置</el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    const vaildateEmail=(rule,value,callback)=>{
      let email=/\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*/;
      if(!email.test(value)){
        callback(new Error('邮箱格式错误'));
      }else{
        callback();
      }
    };
    const vaildateTel=(rule,value,callback)=>{
      let mytel=/^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/;
      if(!mytel.test(value)){
          callback(new Error('手机号格式错误'));
      }else{
          callback();
      }
    };
    const vaildateUserId=(rule,value,callback)=>{
      this.$api.check({//验证用户是否存在
        account:value
      }).then(res=>{
        if(res.err_code === 101){
          callback(res.err_msg);
        }else{
          callback()
        }
      })
    };
    return{
      ruleForm:{
        userId:'',
        password:'',
        telephone:'',
        phoneCode:'',
        userName:'',
        userEmail:''
      },
      rules:{
        userName:[
          { required:true,message:'请填写用户名',trigger:'blur'},
        ],
        userId:[
          { required:true,message:'请填写相应账号',trigger:'blur'},
          { validator:vaildateUserId,trigger:'blur'}
        ],
        password:[
          { required:true,message:'请填写密码',trigger:'blur'},
          { min:6,max:16,message:'密码长度为6至16',trigger:'blur'}
        ],
        userEmail:[
          { required:true,message:'请填写个人邮箱',trigger:'blur'},
          { validator:vaildateEmail,trigger:'blur'}
        ],
        telephone:[
          { required:true,message:'请输入手机号',trigger:'blur'},
          { validator:vaildateTel,trigger:'blur'}
        ],
        phoneCode:[
          { required:true,message:'请输入手机验证码',trigger:'blur'}
        ]
      },
      isCode:false,
      retime:'30'
    }
  },
  methods:{
    sendCode(){//手机验证码
        let that=this;
        if(!that.ruleForm.telephone){
          return that.$message('请输入手机号');
        }
        if(!that.ruleForm.userId){
          return that.$message('请先输入用户账号');
        }
        this.$message(`验证码已发送至${this.ruleForm.telephone},请注意查收`);
        that.isCode=true;
        that.reTime();
        this.$api.code({
            account:that.ruleForm.userId,
            phone: that.ruleForm.telephone
        });
    },
    reTime(){//倒计时
       let timer = setInterval(()=>{
            if(this.retime === 0){
              clearInterval(timer);
              this.isCode=false;
            }else{
              this.retime --;
            }
       },1000)
    },
    submitForm(formName){//注册
      let that=this;
      this.$refs[formName].validate((valid)=>{
          if(valid){
            that.$api.register({
              account:that.ruleForm.userId,
              code:that.ruleForm.phoneCode,
              nickName:that.ruleForm.userName,
              userEmail:that.ruleForm.userEmail,
              userId:that.ruleForm.userId,
              userPass:that.ruleForm.password,
              userPhone:that.ruleForm.telephone
            }).then(res=>{
              if(res.err_code === 100){
                that.$message('账号注册成功');
                that.$refs[formName].resetFields();
              }else if(res.err_code === 101){
                that.$message('验证码错误');
              }else{
                that.$message('账号注册失败');
              }
            })
        }else{
          return that.$message('未按要求填写,无法注册');
        }
      })
    },
    resetForm(formName){
      this.$refs[formName].resetFields();
    }
  }
}
</script>

<style lang="scss" scoped>
    #register{
      width: 800px;
      padding: 50px 0;
      margin: auto;
      
      .register-form{
          width: 500px;
          margin: auto;
          box-sizing: border-box;
          padding: 10px;
          background: #fff;
          
          .register-inner{
              width: 100%;
              height: 100%;
              border: 1.2px solid #4579c7;
              padding: 10px 26px;
              box-sizing: border-box;

              h2{
                font-weight: 500;
                border-bottom: 1px dashed #333;
                text-align: left;
                p.f14{
                    font-weight: 800;
                    font-size: 14px;
                    color: #d5d5d5;
                }
              }
          }
          .el-form{
              margin-top: 10px;
          }
          .toResister{
              text-align: right;
              a{
              font-size: 14px;
              }
          }
      }
      .school{
          display: flex;
          justify-content: center;

          .demonstration{
          margin: 0 12px;
          }
      }
      .telePhone{
          display: flex;
          justify-content: center;

          .el-button{
          margin-left:10px;
          }
      }
    }
</style>